.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
  background-color: #282c34; /* Dark background for contrast */
}

.App-header input, .App-header button {
  font-size: 1.2rem; /* Larger font size for better readability */
  padding: 10px 20px; /* Ample padding for a larger, more accessible touch target */
  margin: 5px; /* Spacing between elements */
  border: none; /* Remove default border */
  border-radius: 5px; /* Rounded corners for a modern look */
  transition: all 0.3s ease; /* Smooth transition for interactive states */
}

.App-header input {
  width: 60%; /* Responsive width */
  max-width: 400px; /* Maximum width to maintain usability */
  background-color: #fff; /* Light background for the input */
  color: #333; /* High contrast text */
}

.App-header input:focus {
  outline: none; /* Remove focus outline */
  box-shadow: 0 0 0 2px #61dafb; /* Custom focus style to match React's color */
}

.App-header button {
  cursor: pointer; /* Indicate clickable */
  background-color: #61dafb; /* Use React's blue for brand consistency */
  color: #282c34; /* Contrast color for readability */
}

.App-header button:hover {
  background-color: #52c7e8; /* Slightly darker blue on hover */
}

.App-header button:disabled {
  background-color: #a0d1e6; /* Lighter blue to indicate disabled state */
  cursor: not-allowed; /* Indicate not clickable */
}

